@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Karla:wght@700&display=swap);
body {
  margin: 0;
  font-family: 'Inconsolata', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  font-family: 'Karla', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:start;padding:32pt;display:flex;flex-direction:column}.flexRow{justify-content:space-between;display:flex;flex-direction:row;flex-wrap:wrap;height:100%}.title{z-index:1;position:relative;min-height:10vh;display:flex;flex-direction:column;justify-content:center;font-size:calc(24px + 3vmin);color:black}.underline-title{font-size:calc(24px + 3vmin);position:relative;z-index:0;transform:translate(-2%, -105%);background-color:#ffc700;max-width:35%;min-width:380px;min-height:38px;-webkit-animation:1s cubic-bezier(0.075, 0.82, 0.165, 1) 0s slide-in-title;animation:1s cubic-bezier(0.075, 0.82, 0.165, 1) 0s slide-in-title;justify-content:right}.h1{flex-direction:column;position:relative;z-index:1;min-height:7vh;display:flex;justify-content:center;font-size:calc(18px + 3vmin);color:black}.underline-h1{position:relative;z-index:0;transform:translate(-2%, -75%);background-color:#ffc700;max-width:25%;min-width:250px;min-height:33px;-webkit-animation:1s cubic-bezier(0.075, 0.82, 0.165, 1) 300ms slide-in-h1;animation:1s cubic-bezier(0.075, 0.82, 0.165, 1) 300ms slide-in-h1;justify-content:right}p.limited-width{max-width:600px;padding-bottom:50px}.url{color:black;-webkit-text-decoration-color:light-grey;text-decoration-color:light-grey;-webkit-text-decoration-line:underline;text-decoration-line:underline;-webkit-text-decoration-skip-ink:none;text-decoration-skip-ink:none;transition:-webkit-text-decoration-color 500ms ease-in 0s 1 slideInFromLeft;transition:text-decoration-color 500ms ease-in 0s 1 slideInFromLeft;transition:text-decoration-color 500ms ease-in 0s 1 slideInFromLeft, -webkit-text-decoration-color 500ms ease-in 0s 1 slideInFromLeft}.url:hover{-webkit-text-decoration-skip-ink:none;text-decoration-skip-ink:none;-webkit-text-decoration-color:#ffc700;text-decoration-color:#ffc700;-webkit-text-decoration-line:underline;text-decoration-line:underline}.image-container{justify-content:center;max-width:700px;max-height:350px;min-width:300px;margin:0px auto 0px auto;padding-bottom:50px}img.coverPhoto{position:relative;z-index:2;-webkit-filter:drop-shadow(2px 4px 10px rgba(0,0,0,0.25));filter:drop-shadow(2px 4px 10px rgba(0,0,0,0.25));object-fit:cover;border-radius:24px;height:100%;width:100%}.yellow-cover-decoration{position:relative;z-index:1;width:70%;height:70%;background-color:#ffc700;transform:translate(-5%, -155%)}.black-cover-decoration{position:relative;z-index:1;width:71%;height:70%;background-image:radial-gradient(#ebebeb 20%, transparent 20%);background-color:white;transform:translate(50%, -185%);background-position:0 0, 25px 25px;background-size:20px 20px}.dot-pattern-decoration{transform:translate(5%, 20%);position:relative;z-index:0;width:70%;height:70%;background:linear-gradient(90deg, #fff 21px, transparent 1%) center,linear-gradient(#fff 21px, transparent 1%) center,#000;background-size:22px 22px}@-webkit-keyframes slide-in-title{from{transform:translate(-100%, -105%) scaleX(0)}to{transform:translate(-2%, -105%) scaleX(1)}}@keyframes slide-in-title{from{transform:translate(-100%, -105%) scaleX(0)}to{transform:translate(-2%, -105%) scaleX(1)}}@-webkit-keyframes slide-in-h1{from{transform:translate(-100%, -75%) scaleX(0)}to{transform:translate(-2%, -75%) scaleX(1)}}@keyframes slide-in-h1{from{transform:translate(-100%, -75%) scaleX(0)}to{transform:translate(-2%, -75%) scaleX(1)}}

